import Vue from 'vue'
import Router from 'vue-router'
import Window from '../components/Window'
import Numbers from '../components/Numbers'

Vue.use(Router)

export default new Router({
	routes:[
		{
			path:'/Window',
			component:Window
		},{
			path:'/Numbers',
			component:Numbers
		}
	]
})


