<template>
	<div id="app">
		<input type="number" v-model="num" class="window">
		<br>
		<template v-for="(data,index) in item">			
			<input type="button" v-model="data.numb" @click="thisNumber(index)">
		</template>

		<br>
		<input type="button" @click="add" value="+">	
		<input type="button" @click="minus" value="-">	
		<input type="button" @click="multiplicative" value="×">	
		<input type="button" @click="division" value="÷">	
		<input type="button" @click="equal" value="=">	
		<input type="button" @click="clear" value="c">	
		<input type="button" @click="nagative" value="±">	
	</div>
</template>
<script>
	export default {
		name:'app',
		flag:"",
		cls:false,
		data(){
			return{
				num:0,
				num2:0,
				item:[{numb:0},{numb:1},{numb:2},{numb:3},{numb:4},{numb:5},{numb:6},{numb:7},{numb:8},{numb:9}]
			}
		},
		methods:{
			add:function(){
				this.num2=this.num;
				this.num=0;
				this.flag="+"
			},
			minus:function(){
				this.num2=this.num;
				this.num=0;
				this.flag="-"
			},
			multiplicative:function(){
				this.num2=this.num;
				this.num=0;
				this.flag="*"
			},
			division:function(){
				this.num2=this.num;
				this.num=0;
				this.flag="/"
			},
			equal:function(){
				if(this.flag==""){
					this.num=this.num
				}
				else if(this.flag=="+"){
					this.num=this.num+this.num2
				}
				else if(this.flag=="-"){
					this.num=this.num2-this.num
				}
				else if(this.flag=="*"){
					this.num=this.num*this.num2
				}
				else if(this.flag=="/"){
					if (this.num==0) {
						this.num=prompt("error：被除数不能为0, 请重新输入被除数：","")
						this.num=this.num2/this.num
					}else{
						this.num=this.num2/this.num
					}
				};
				this.flag=""
				this.cls=true;
			},
			clear:function(){
				this.num=0;
				this.flag=""
			},
			nagative:function(){
				this.num=-this.num;
			},	
			thisNumber:function(index){
				if(!this.cls){
					this.num=this.num*10+this.item[index].numb
				}
				else if(this.cls){
					this.num=0;
					this.num=this.num*10+this.item[index].numb
					this.cls=false
				}
			}
		}
	}
</script>
<style>
	#app{
		margin-left: 200px;
	}
	input::-webkit-outer-spin-button,
	input::-webkit-inner-spin-button {
	  -webkit-appearance: none;
	}
	input[type="number"]{
	  -moz-appearance: textfield;
	}
	input[type='button']{
		width: 23px;
		height: 23px;
	}
	.window{
		text-align: right;
		padding: 2px 6px
	}
</style>


